import { useState, useEffect } from 'react';
import { useNavigate, useSearchParams, Link } from 'react-router-dom';
import { collection, getDocs, addDoc, doc, getDoc } from 'firebase/firestore';
import { db } from '../../firebase';
import { useNotification } from './NotificationSystem';
import { 
  FiArrowLeft, FiCalendar, FiDollarSign, FiUser, FiFileText, 
  FiClock, FiTool, FiShield, FiAward, FiEye, FiPlus, FiMinus,
  FiCheckCircle, FiAlertTriangle, FiSettings
} from 'react-icons/fi';

export default function JobAssignmentForm() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const { showSuccess, showError } = useNotification();
  const [loading, setLoading] = useState(false);
  const [vendors, setVendors] = useState([]);
  const [selectedVendor, setSelectedVendor] = useState(null);
  const [formData, setFormData] = useState({
    vendorId: searchParams.get('vendorId') || '',
    jobTitle: '',
    description: '',
    requirements: '',
    budget: '',
    deadline: '',
    priority: 'medium',
    status: 'assigned',
    location: '',
    contactPerson: '',
    contactEmail: '',
    contactPhone: '',
    deliverables: [],
    milestones: [],
    // Enhanced fields
    jobType: 'machining',
    estimatedHours: '',
    skillsRequired: [],
    materials: [],
    safetyRequirements: '',
    qualityStandards: '',
    paymentTerms: 'net30',
    currency: 'INR',
    taxRate: '18',
    advancePayment: '',
    penaltyClause: '',
    bonusClause: '',
    workingHours: '8',
    overtimeRate: '',
    equipmentRequired: [],
    inspectionRequired: false,
    certificationRequired: false,
    insuranceRequired: false
  });

  const [errors, setErrors] = useState({});
  const [showPreview, setShowPreview] = useState(false);

  useEffect(() => {
    fetchVendors();
    if (formData.vendorId) {
      fetchVendorDetails(formData.vendorId);
    }
  }, [formData.vendorId]);

  const fetchVendors = async () => {
    try {
      const vendorsSnapshot = await getDocs(collection(db, 'vendors'));
      const vendorsData = [];
      vendorsSnapshot.forEach((doc) => {
        const vendor = { id: doc.id, ...doc.data() };
        if (vendor.status === 'active') {
          vendorsData.push(vendor);
        }
      });
      setVendors(vendorsData);
    } catch (error) {
      console.error('Error fetching vendors:', error);
      showError('Failed to load vendors');
    }
  };

  const fetchVendorDetails = async (vendorId) => {
    try {
      const vendorDoc = await getDoc(doc(db, 'vendors', vendorId));
      if (vendorDoc.exists()) {
        setSelectedVendor({ id: vendorDoc.id, ...vendorDoc.data() });
      }
    } catch (error) {
      console.error('Error fetching vendor details:', error);
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.vendorId) newErrors.vendorId = 'Vendor is required';
    if (!formData.jobTitle.trim()) newErrors.jobTitle = 'Job title is required';
    if (!formData.description.trim()) newErrors.description = 'Description is required';
    if (!formData.deadline) newErrors.deadline = 'Deadline is required';
    if (formData.budget && isNaN(formData.budget)) newErrors.budget = 'Budget must be a number';
    if (formData.estimatedHours && isNaN(formData.estimatedHours)) newErrors.estimatedHours = 'Estimated hours must be a number';
    if (formData.taxRate && (isNaN(formData.taxRate) || formData.taxRate < 0 || formData.taxRate > 100)) {
      newErrors.taxRate = 'Tax rate must be between 0-100';
    }
    
    // Validate deadline is in future
    if (formData.deadline && new Date(formData.deadline) <= new Date()) {
      newErrors.deadline = 'Deadline must be in the future';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const calculateTotalCost = () => {
    const budget = parseFloat(formData.budget || 0);
    const taxRate = parseFloat(formData.taxRate || 0);
    const advance = parseFloat(formData.advancePayment || 0);
    
    const taxAmount = (budget * taxRate) / 100;
    const totalWithTax = budget + taxAmount;
    const remainingAmount = totalWithTax - advance;
    
    return {
      subtotal: budget,
      taxAmount,
      totalWithTax,
      advancePayment: advance,
      remainingAmount
    };
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      showError('Please fix the validation errors');
      return;
    }
    
    try {
      setLoading(true);
      const costs = calculateTotalCost();
      
      const jobData = {
        ...formData,
        createdAt: new Date(),
        updatedAt: new Date(),
        assignedBy: 'admin',
        deliverables: formData.deliverables.filter(d => d.trim() !== ''),
        milestones: formData.milestones.filter(m => m.title && m.date),
        skillsRequired: formData.skillsRequired.filter(s => s.trim() !== ''),
        materials: formData.materials.filter(m => m.name && m.quantity),
        equipmentRequired: formData.equipmentRequired.filter(e => e.trim() !== ''),
        costBreakdown: costs,
        jobNumber: `JOB-${Date.now()}`,
        estimatedCompletionDate: formData.deadline
      };

      await addDoc(collection(db, 'jobAssignments'), jobData);
      showSuccess('Job assigned successfully');
      navigate('/admin/job-assignments');
    } catch (error) {
      console.error('Error assigning job:', error);
      showError('Failed to assign job');
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    
    if (name === 'vendorId') {
      fetchVendorDetails(value);
    }
  };

  const addDeliverable = () => {
    setFormData(prev => ({
      ...prev,
      deliverables: [...prev.deliverables, '']
    }));
  };

  const updateDeliverable = (index, value) => {
    setFormData(prev => ({
      ...prev,
      deliverables: prev.deliverables.map((item, i) => i === index ? value : item)
    }));
  };

  const removeDeliverable = (index) => {
    setFormData(prev => ({
      ...prev,
      deliverables: prev.deliverables.filter((_, i) => i !== index)
    }));
  };

  const addMilestone = () => {
    setFormData(prev => ({
      ...prev,
      milestones: [...prev.milestones, { title: '', date: '', description: '' }]
    }));
  };

  const updateMilestone = (index, field, value) => {
    setFormData(prev => ({
      ...prev,
      milestones: prev.milestones.map((milestone, i) => 
        i === index ? { ...milestone, [field]: value } : milestone
      )
    }));
  };

  const removeMilestone = (index) => {
    setFormData(prev => ({
      ...prev,
      milestones: prev.milestones.filter((_, i) => i !== index)
    }));
  };

  // Skills management
  const addSkill = () => {
    setFormData(prev => ({
      ...prev,
      skillsRequired: [...prev.skillsRequired, '']
    }));
  };

  const updateSkill = (index, value) => {
    setFormData(prev => ({
      ...prev,
      skillsRequired: prev.skillsRequired.map((skill, i) => i === index ? value : skill)
    }));
  };

  const removeSkill = (index) => {
    setFormData(prev => ({
      ...prev,
      skillsRequired: prev.skillsRequired.filter((_, i) => i !== index)
    }));
  };

  // Materials management
  const addMaterial = () => {
    setFormData(prev => ({
      ...prev,
      materials: [...prev.materials, { name: '', quantity: '', unit: '', cost: '' }]
    }));
  };

  const updateMaterial = (index, field, value) => {
    setFormData(prev => ({
      ...prev,
      materials: prev.materials.map((material, i) => 
        i === index ? { ...material, [field]: value } : material
      )
    }));
  };

  const removeMaterial = (index) => {
    setFormData(prev => ({
      ...prev,
      materials: prev.materials.filter((_, i) => i !== index)
    }));
  };

  // Equipment management
  const addEquipment = () => {
    setFormData(prev => ({
      ...prev,
      equipmentRequired: [...prev.equipmentRequired, '']
    }));
  };

  const updateEquipment = (index, value) => {
    setFormData(prev => ({
      ...prev,
      equipmentRequired: prev.equipmentRequired.map((equipment, i) => i === index ? value : equipment)
    }));
  };

  const removeEquipment = (index) => {
    setFormData(prev => ({
      ...prev,
      equipmentRequired: prev.equipmentRequired.filter((_, i) => i !== index)
    }));
  };

  return (
    <div className="max-w-4xl mx-auto p-6">
      <div className="mb-6">
        <Link
          to="/admin/job-assignments"
          className="text-indigo-600 hover:text-indigo-800 flex items-center mb-4"
        >
          <FiArrowLeft className="mr-2" />
          Back to Job Assignments
        </Link>
        <h1 className="text-2xl font-bold text-gray-900">Assign New Job</h1>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Vendor Selection */}
        <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
          <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
            <FiUser className="mr-2" />
            Vendor Selection
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Select Vendor *
              </label>
              <select
                name="vendorId"
                value={formData.vendorId}
                onChange={handleChange}
                required
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              >
                <option value="">Choose a vendor...</option>
                {vendors.map(vendor => (
                  <option key={vendor.id} value={vendor.id}>
                    {vendor.companyName} - {vendor.category}
                  </option>
                ))}
              </select>
            </div>
            {selectedVendor && (
              <div className="bg-gray-50 p-4 rounded-md">
                <h3 className="text-sm font-medium text-gray-700 mb-2">Vendor Details</h3>
                <p className="text-sm text-gray-600">Contact: {selectedVendor.contactPerson}</p>
                <p className="text-sm text-gray-600">Email: {selectedVendor.email}</p>
                <p className="text-sm text-gray-600">Phone: {selectedVendor.phone}</p>
              </div>
            )}
          </div>
        </div>

        {/* Job Details */}
        <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
          <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
            <FiFileText className="mr-2" />
            Job Details
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Job Title *
              </label>
              <input
                type="text"
                name="jobTitle"
                value={formData.jobTitle}
                onChange={handleChange}
                required
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Description *
              </label>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleChange}
                required
                rows={4}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Requirements
              </label>
              <textarea
                name="requirements"
                value={formData.requirements}
                onChange={handleChange}
                rows={3}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Job Type *
              </label>
              <select
                name="jobType"
                value={formData.jobType}
                onChange={handleChange}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              >
                <option value="machining">Machining</option>
                <option value="fabrication">Fabrication</option>
                <option value="assembly">Assembly</option>
                <option value="welding">Welding</option>
                <option value="inspection">Inspection</option>
                <option value="maintenance">Maintenance</option>
                <option value="consulting">Consulting</option>
                <option value="other">Other</option>
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Budget (â‚¹)
              </label>
              <input
                type="number"
                name="budget"
                value={formData.budget}
                onChange={handleChange}
                className={`w-full border rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500 ${
                  errors.budget ? 'border-red-300' : 'border-gray-300'
                }`}
              />
              {errors.budget && <p className="text-red-500 text-sm mt-1">{errors.budget}</p>}
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Estimated Hours
              </label>
              <input
                type="number"
                name="estimatedHours"
                value={formData.estimatedHours}
                onChange={handleChange}
                className={`w-full border rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500 ${
                  errors.estimatedHours ? 'border-red-300' : 'border-gray-300'
                }`}
              />
              {errors.estimatedHours && <p className="text-red-500 text-sm mt-1">{errors.estimatedHours}</p>}
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Deadline *
              </label>
              <input
                type="date"
                name="deadline"
                value={formData.deadline}
                onChange={handleChange}
                required
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Priority
              </label>
              <select
                name="priority"
                value={formData.priority}
                onChange={handleChange}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              >
                <option value="low">Low</option>
                <option value="medium">Medium</option>
                <option value="high">High</option>
                <option value="urgent">Urgent</option>
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Location
              </label>
              <input
                type="text"
                name="location"
                value={formData.location}
                onChange={handleChange}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
          </div>
        </div>

        {/* Contact Information */}
        <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">Contact Information</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Contact Person
              </label>
              <input
                type="text"
                name="contactPerson"
                value={formData.contactPerson}
                onChange={handleChange}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Contact Email
              </label>
              <input
                type="email"
                name="contactEmail"
                value={formData.contactEmail}
                onChange={handleChange}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Contact Phone
              </label>
              <input
                type="tel"
                name="contactPhone"
                value={formData.contactPhone}
                onChange={handleChange}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
          </div>
        </div>

        {/* Deliverables */}
        <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-lg font-semibold text-gray-900">Deliverables</h2>
            <button
              type="button"
              onClick={addDeliverable}
              className="px-3 py-1 bg-indigo-100 text-indigo-700 rounded-md hover:bg-indigo-200 text-sm"
            >
              Add Deliverable
            </button>
          </div>
          {formData.deliverables.map((deliverable, index) => (
            <div key={index} className="flex items-center space-x-2 mb-2">
              <input
                type="text"
                value={deliverable}
                onChange={(e) => updateDeliverable(index, e.target.value)}
                placeholder="Enter deliverable..."
                className="flex-1 border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
              <button
                type="button"
                onClick={() => removeDeliverable(index)}
                className="px-2 py-2 text-red-600 hover:text-red-800"
              >
                Remove
              </button>
            </div>
          ))}
        </div>

        {/* Milestones */}
        <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-lg font-semibold text-gray-900 flex items-center">
              <FiCalendar className="mr-2" />
              Milestones
            </h2>
            <button
              type="button"
              onClick={addMilestone}
              className="px-3 py-1 bg-indigo-100 text-indigo-700 rounded-md hover:bg-indigo-200 text-sm"
            >
              Add Milestone
            </button>
          </div>
          {formData.milestones.map((milestone, index) => (
            <div key={index} className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4 p-4 border border-gray-200 rounded-md">
              <div>
                <input
                  type="text"
                  value={milestone.title}
                  onChange={(e) => updateMilestone(index, 'title', e.target.value)}
                  placeholder="Milestone title..."
                  className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                />
              </div>
              <div>
                <input
                  type="date"
                  value={milestone.date}
                  onChange={(e) => updateMilestone(index, 'date', e.target.value)}
                  className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                />
              </div>
              <div className="flex items-center space-x-2">
                <input
                  type="text"
                  value={milestone.description}
                  onChange={(e) => updateMilestone(index, 'description', e.target.value)}
                  placeholder="Description..."
                  className="flex-1 border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                />
                <button
                  type="button"
                  onClick={() => removeMilestone(index)}
                  className="px-2 py-2 text-red-600 hover:text-red-800"
                >
                  Remove
                </button>
              </div>
            </div>
          ))}
        </div>

        <div className="flex justify-end space-x-4">
          <Link
            to="/admin/job-assignments"
            className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
          >
            Cancel
          </Link>
          <button
            type="submit"
            disabled={loading}
            className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 disabled:opacity-50 flex items-center"
          >
            {loading && <div className="animate-spin rounded-full h-4 w-4 border-t-2 border-b-2 border-white mr-2"></div>}
            Assign Job
          </button>
        </div>
      </form>
    </div>
  );
}
